<template>
  <v-bottom-navigation v-model="value"
                       :background-color="changeColor"
                       absolute
  >
    <v-btn :href="'/#'+hrefs[0]">
      <span>{{ bottomNavi[0] }}</span>
      <v-icon>{{ bottomIcon[0] }}</v-icon>
    </v-btn>
    <v-btn :href="'/#'+hrefs[1]">
      <span>{{ bottomNavi[1] }}</span>
      <v-icon>{{ bottomIcon[1] }}</v-icon>
    </v-btn>
    <v-btn :href="'/#'+hrefs[2]">
      <span>{{ bottomNavi[2] }}</span>
      <v-icon>{{ bottomIcon[2] }}</v-icon>
    </v-btn>
  </v-bottom-navigation>
</template>

<script>
export default {
  props: {
    bottomNavi: {
      type: Array
    },
    bottomIcon: {
      type: Array
    },
    hrefs:{
      type:Array
    }
  },
  name: "Bottom",
  data: () => ({
    value: 1
  }),
  computed: {
    changeColor() {
      switch (this.value) {
        case 0:
          return 'blue-grey'
        case 1:
          return 'teal'
        case 2:
          return 'brown'
        case 3:
          return 'indigo'
        default:
          return 'blue-grey'
      }
    }
  }
}
</script>

